$colorV:#00BFA6;
$colorOr:#F9A826;
$colorR:#F50057;
$colorW:#FFFFFF;
$colorB:rgba(0, 0, 0, 0.527);
$colorX:#ccc;

body {
    padding: 0;
    margin: 0;
    
}
button:focus, input:focus, textarea:focus , select:focus{
    outline: none;
}


nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .5em;
    background-color: $colorV;

    a {
        text-decoration: none;
        color: $colorW;
    }

    div {
        display: flex;
        align-items: center;

        h4 {
            margin-right: 10px;
            margin-top: 30px;

            font-size: medium;
            font-weight: 500;
        }

        img {
            width: 35px;
            height: 35px;
            object-fit: cover;
            border-radius: 20px;
        }
    }
}

.chart {
    .chart_title {
        h2 {
            font-weight: 500;
            width: 347px;
            border-bottom: 3px solid $colorOr;
            margin-top: -20px;

            @media only screen and (max-width :475px) {
                font-size: 17px;
                width: 246px;

            }

        }
    }

    .chart_ui {
        display: flex;
        align-items: center;
        width: 80%;
        margin: 10px auto;
        justify-content: space-evenly;
        flex-wrap: wrap;
        padding: 0;

        @media only screen and (max-width :500px) {
            width: 100%;
        }

        .chart_UI {
            height: 15px;
            width: 40%;
            border: 1px solid $colorR;
            border-radius: 10px;

            @media only screen and (max-width :670px) {
                width: 80%;
            }

            .chart_UI1 {
                width: 60%;
                height: 15px;
                background-color: $colorR;
                border-radius: 10px;

            }

        }

        .chart_2 {
            @extend .chart_UI;
            border: 1px solid $colorOr;

            .chart_UI2 {
                @extend .chart_UI1;
                background-color: $colorOr;
                width: 30%;

            }

        }

        .chart_3 {
            @extend .chart_UI;
            border: 1px solid $colorV;

            .chart_UI3 {
                @extend .chart_UI1;
                background-color: $colorV;
                width: 20%;

            }

        }

        .chart_4 {
            @extend .chart_UI;

            .chart_UI4 {
                @extend .chart_UI1;
                width: 10%;


            }

        }
    }
}




.container {
    margin: 2em auto;
    max-width: 71vw;
    box-shadow: 6px 7px 19px 3px rgba(149, 152, 154, 0.554);
    padding: 2em;
    border-radius: .3em;

    .check {
        .name-and-pen {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .pen_img {
                width: 18px;
            }

            .profil_img {
                width: 60px;
                height: 60px;
                border-radius: 50%;
                object-fit: cover;
            }

            .name-img {
                display: flex;
            }

            h2 {
                margin-left: 5px;
                font-weight: 500;
                font-size: x-large;
                ;
                margin-right: 10px;

                @media only screen and (max-width: 540px) {
                    font-size: medium;

                }
            }
        }



        input {
            border: 1px solid $colorOr;
            border-radius: 5px;
            padding: 11px;
            font-size: medium;
            font-weight: normal;
            width: 270px;
            height: 21px;


            @media only screen and (max-width: 700px) {
                width: 80%;
            }
        }


    }

    .inputs {
        margin-top: 15px;
        display: flex;

        flex-wrap: wrap;

        div {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            margin: 8px;
        }

        label {
            font-weight: 400;
            padding-right: 6px;
        }
    }

}

.historique {
    font-weight: 500;
    width: 123px;
    border-bottom: 3px solid $colorOr;
    margin-top: -20px;

    @media only screen and (max-width :475px) {
        font-size: 18px;
        width: 100px;

    }

}

.h2demnd {
    font-size: 1.3vw;
}

.activité {
    color: rgba(149, 152, 154, 0.781);
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;

}

.agenda {
    text-align: center;
    padding-bottom: 3em;

    // width: 76vw;
    margin: auto;

    height: auto;


    iframe {
        box-shadow: 6px 7px 19px 3px rgba(149, 152, 154, 0.554);
        width: 74vw;
        font-size: large;
        padding: 10px;
        border-radius: .3em;
        overflow: hidden;
    }
}

.btn_add_event {
    width: 170px;
    padding: 15px;
    background-color: $colorOr;
    color: $colorW;
    border: none;
    border-radius: 49px;
    margin: auto;
    display: flex;
    justify-content: center;
    font-size: medium;
    box-shadow: 6px 7px 19px 3px rgba(149, 152, 154, 0.554);

}

.pop-up-add_events {
    width: 100%;
    height: 215%;
    position: absolute;
    top: 0;
    background-color: $colorB;
    z-index: 1111;
    display: flex;
    justify-content: center;
}

.pop-up-add_event {
    width: 38%;
    height: auto;
    // margin: auto;
    background-color: $colorW;
    padding: 20px;
    position: fixed;
   
    // left: 29%;
    top: 13%;
    border-radius: 5px;
    @media only screen and (max-width: 900px) {
        width: 60%;
        
    }
    @media only screen and (max-width: 600px) {
        width: 80%;
        
    }
    .clouse{
        height: 20px;
        svg{
            width: 20px;
            float: right;
            margin-top: -10px;
            
        }
    
        
    }
   
    .pop-up-add_event_matairs {
        display: flex;
        flex-wrap: wrap;
        

        select {
            width: 260px;
            height: 50px;
            border-radius: 5px;
            border: 1px solid black;
            font-weight: 500;
            font-size: medium;
            margin-bottom: 10px;
            font-family: 'Poppins', sans-serif;
            // background-color: rgba(0, 0, 0, 0.164);
            
        }

    }

    .hour {
        margin-left: 20px;
        @media only screen and (max-width: 1125px) {
            margin-left: 0;
            
        }
     
        @media only screen and (max-width: 900px) {
            margin-left: 20px;
            
        }
        @media only screen and (max-width: 712px) {
            margin-left: 20px;
            
        }
        @media only screen and (max-width: 660px ) {
            margin-left: 0;
            
        }
        @media only screen and (max-width: 600px) {
            margin-left: 20px;
            
        }
        @media only screen and (max-width: 533px ) {
            margin-left: 0;
            
        }
    }
    .hour2 {
        margin-left: 20px;
        @media only screen and (max-width: 1420px) {
            margin-left: 0;
            
        }
    }

    .Date {
        display: flex;
        flex-wrap: wrap;
        // justify-content: space-evenly;
        

        .thedate {
            // margin-left:10px ;
            width: 200px;
            height: 50px;
            border-radius: 5px;
            border: 1px solid $colorOr;
            font-weight: 500;
            font-size: medium;
            text-align: center;
            font-family: 'Poppins', sans-serif;
            margin-bottom: 10px;
        }
    }

    .lien_for_the_meeting {
        width: 96%;
        height: 50px;
        border-radius: 5px;
        border: 1px solid $colorR;
        font-weight: 500;
        font-size: medium;
        // text-align: center;
        margin-bottom: 10px;
        padding-left: 10px;
    }

    .message {
        margin-top: 10px;
        width: 95%;
        height: 130px;
        padding: 10px;
        font-size: medium;
        font-weight: 500;
        font-family: 'Poppins', sans-serif;
        border-radius: 5px;
        border: 1px solid $colorV;

    }

    button {
        width: 170px;
        padding: 15px;
        background-color: $colorOr;
        color: $colorW;
        border: none;
        border-radius: 49px;
        margin: 10px auto;
        display: flex;
        justify-content: center;
        font-size: medium;
        font-weight: 600;
    }


}

footer {
    background-color: $colorV;
    padding: 10px;
    margin-top: 26px;

    .Copyright {
        margin-left: 20px;
        font-weight: 500;
    }

    .footer {
        position: relative;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        ;
        background-color: $colorV;
        padding: 20px;
        align-items: center;

        @media only screen and (max-width :475px) {
            // padding: 15px;
            // width: 120px;
            // font-size: small;
            justify-content: center;
        }

        &__info {



            h1 {
                color: $colorW;
            }

            h3 {
                color: $colorW;
                font-size: 1em;
            }
        }

        &__serv {
            // padding-top: 2em;

            .btn-find-help,
            .btn-give-help {
                display: inline-block;
                width: 170px;
                background-color: $colorOr;
                padding: 18px;
                border-radius: 40px;
                text-decoration: none;
                color: $colorW;
                font-weight: bold;
                text-align: center;
                margin: 20px;

                @media only screen and (max-width :475px) {
                    padding: 15px;
                    width: 120px;
                    font-size: small;
                    justify-content: center;
                }

            }

            .btn-give-help {
                background-color: $colorR;
            }
        }

        &__social {
            svg {
                width: 45px;
                margin-right: 10px;
                margin-top: 10px;

            }
        }

    }

    @media only screen and (max-width:500px) {
        .footer {

            &__serv {
                padding-top: 0em;
            }

            &__social {
                img {
                    width: 15%;
                }
            }
        }
    }
}